
'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Button } from '@/components/ui/button';
import type { AttendanceRecord, LoggedInUser, Employee } from '@/lib/types'; // Import Employee
import { format, parseISO } from 'date-fns';
import { History, User, Calendar, Clock, MapPin, Camera, Download, AlertTriangle, UserCog, Cog } from 'lucide-react'; // Added Cog
import { useToast } from '@/hooks/use-toast';
import * as XLSX from 'xlsx';
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import Image from 'next/image';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';

// Helper function to get employees from localStorage
const getStoredEmployees = (): Employee[] => {
    if (typeof window === 'undefined') return [];
    const stored = localStorage.getItem('employees');
    try {
        return stored ? JSON.parse(stored) : [];
    } catch (e) {
        console.error("Failed to parse employees from localStorage:", e);
        return [];
    }
};


export default function AdminPage() {
  const router = useRouter();
  const { toast } = useToast();
  const [history, setHistory] = useState<AttendanceRecord[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isAdmin, setIsAdmin] = useState(false);
  const [employees, setEmployees] = useState<Employee[]>([]); // State to hold employee data

  useEffect(() => {
    // Check login status and admin role
    const loggedInUserString = localStorage.getItem('loggedInUser');
    if (!loggedInUserString) {
      toast({
        variant: 'destructive',
        title: 'Not Logged In',
        description: 'Please log in as admin to view this page.',
      });
      router.push('/login');
      return;
    }

    try {
      const user: LoggedInUser = JSON.parse(loggedInUserString);
      if (user.type !== 'admin') { // Check type property
        toast({
          variant: 'destructive',
          title: 'Access Denied',
          description: 'You do not have permission to access this page.',
        });
        router.push('/'); // Redirect non-admins to the home page
        return;
      }
      setIsAdmin(true); // User is admin
    } catch (e) {
      console.error("Failed to parse user data", e);
      toast({
        variant: 'destructive',
        title: 'Login Error',
        description: 'Invalid login data. Please log in again.',
      });
      localStorage.removeItem('loggedInUser');
      router.push('/login');
      return;
    }

    // Fetch employee data
    setEmployees(getStoredEmployees());

    // Fetch attendance data from localStorage if admin
    const storedHistory = localStorage.getItem('attendanceHistory');
    if (storedHistory) {
      try {
        const parsedHistory: AttendanceRecord[] = JSON.parse(storedHistory)
          .map((record: any) => ({
            ...record,
            // Ensure timestamp is a Date object - handle potential string format
            timestamp: typeof record.timestamp === 'string' ? parseISO(record.timestamp) : new Date(record.timestamp),
            // Ensure attendanceMethod exists, default to 'Manual' if missing (for older records)
            attendanceMethod: record.attendanceMethod || 'Manual',
          }))
          .sort((a: AttendanceRecord, b: AttendanceRecord) => b.timestamp.getTime() - a.timestamp.getTime()); // Sort newest first
        setHistory(parsedHistory);
      } catch (e) {
        console.error("Failed to parse attendance history:", e);
        setHistory([]);
        toast({
          variant: 'destructive',
          title: 'History Error',
          description: 'Could not load attendance history.',
        });
      }
    }
    setIsLoading(false);
  }, [router, toast]);

  // Function to get Employee official ID by phone number
  const getEmployeeOfficialId = (phoneNumber: string): string => {
      const employee = employees.find(emp => emp.phoneNumber === phoneNumber);
      return employee?.employeeId || 'N/A'; // Return official ID or N/A
  }

  const processAttendanceForReport = (records: AttendanceRecord[]) => {
    const reportData: Record<string, any>[] = [];
    const groupedByUserAndDay: Record<string, Record<string, AttendanceRecord[]>> = {};

    // Group records by user ID (phone number) and then by day
    records.forEach(record => {
        const dayKey = format(record.timestamp, 'yyyy-MM-dd');
        if (!groupedByUserAndDay[record.userId]) {
            groupedByUserAndDay[record.userId] = {};
        }
        if (!groupedByUserAndDay[record.userId][dayKey]) {
            groupedByUserAndDay[record.userId][dayKey] = [];
        }
        groupedByUserAndDay[record.userId][dayKey].push(record);
    });

    // Process each user's daily records
    Object.keys(groupedByUserAndDay).forEach(userId => {
        const userDays = groupedByUserAndDay[userId];
        Object.keys(userDays).forEach(dayKey => {
            const dailyRecords = userDays[dayKey].sort((a, b) => a.timestamp.getTime() - b.timestamp.getTime()); // Sort oldest first for IN/OUT

            if (dailyRecords.length > 0) {
                const firstRecord = dailyRecords[0];
                const lastRecord = dailyRecords[dailyRecords.length - 1];
                const employeeOfficialId = getEmployeeOfficialId(firstRecord.userId); // Get the official employee ID

                reportData.push({
                    'Employee ID': employeeOfficialId, // Use official employee ID
                    'Employee Name': firstRecord.userName,
                    'Phone Number': firstRecord.userId, // Keep phone number as a separate field
                    'Date': dayKey,
                    'In Time': format(firstRecord.timestamp, 'HH:mm:ss'),
                    'In Location (Lat)': firstRecord.location.latitude.toFixed(5),
                    'In Location (Lon)': firstRecord.location.longitude.toFixed(5),
                    'In Address': firstRecord.location.address || 'N/A',
                    'In Method': firstRecord.attendanceMethod, // Add In Method
                    'Out Time': dailyRecords.length > 1 ? format(lastRecord.timestamp, 'HH:mm:ss') : 'N/A', // Only show Out Time if more than one record
                    'Out Location (Lat)': dailyRecords.length > 1 ? lastRecord.location.latitude.toFixed(5) : 'N/A',
                    'Out Location (Lon)': dailyRecords.length > 1 ? lastRecord.location.longitude.toFixed(5) : 'N/A',
                    'Out Address': dailyRecords.length > 1 ? (lastRecord.location.address || 'N/A') : 'N/A',
                    'Out Method': dailyRecords.length > 1 ? lastRecord.attendanceMethod : 'N/A', // Add Out Method
                    // Optionally add total hours later
                });
            }
        });
    });

    // Sort final report data by date (descending) then employee name (ascending)
    reportData.sort((a, b) => {
        const dateComparison = b.Date.localeCompare(a.Date);
        if (dateComparison !== 0) return dateComparison;
        return a['Employee Name'].localeCompare(b['Employee Name']);
    });


    return reportData;
};


  const handleDownload = () => {
    if (!history.length) {
        toast({
            variant: 'destructive',
            title: 'No Data',
            description: 'There is no attendance data to download.',
        });
        return;
    }

    // Prepare data using the processing function
    const dataToExport = processAttendanceForReport(history);

    if (!dataToExport.length) {
         toast({
            variant: 'destructive',
            title: 'Processing Error',
            description: 'Could not process data for the report.',
        });
        return;
    }

    // Create worksheet and workbook
    const worksheet = XLSX.utils.json_to_sheet(dataToExport);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, 'Attendance Report');

    // Set column widths (adjust as needed)
    worksheet['!cols'] = [
        { wch: 15 }, // Employee ID
        { wch: 25 }, // Employee Name
        { wch: 18 }, // Phone Number
        { wch: 12 }, // Date
        { wch: 10 }, // In Time
        { wch: 18 }, // In Lat
        { wch: 18 }, // In Lon
        { wch: 40 }, // In Address
        { wch: 12 }, // In Method
        { wch: 10 }, // Out Time
        { wch: 18 }, // Out Lat
        { wch: 18 }, // Out Lon
        { wch: 40 }, // Out Address
        { wch: 12 }, // Out Method
    ];

    // Generate Excel file and trigger download
    const fileName = `Attendance_Report_${format(new Date(), 'yyyyMMdd_HHmmss')}.xlsx`;
    XLSX.writeFile(workbook, fileName);

    toast({
        title: 'Download Started',
        description: `Downloading ${fileName}`,
    });
  };

  // Render loading or access denied message if necessary
  if (!isAdmin && !isLoading) {
      return (
         <div className="flex justify-center items-center h-full">
            <Alert variant="destructive" className="max-w-md">
                 <AlertTriangle className="h-4 w-4"/>
                 <AlertTitle>Access Denied</AlertTitle>
                 <AlertDescription>
                    You must be logged in as an administrator to view this page.
                 </AlertDescription>
            </Alert>
         </div>
      );
  }
   if (isLoading) {
       return <div className="text-center p-10">Loading Admin Dashboard...</div>;
   }


  return (
    <Card className="shadow-lg">
      <CardHeader>
        <CardTitle className="text-2xl font-bold text-center text-primary flex items-center justify-center gap-2">
           <UserCog className="h-6 w-6" /> Admin Dashboard - All Attendance Logs
        </CardTitle>
        <CardDescription className="text-center">
          View individual clock-in/out events. Download the consolidated report for daily summaries.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <ScrollArea className="h-[60vh] rounded-md border">
          <Table>
            <TableCaption>
              {isLoading
                ? 'Loading attendance history...'
                : history.length === 0
                ? 'No attendance records found.'
                : 'A list of all recent clock-in/out records. Use Download for daily summary.'}
            </TableCaption>
            <TableHeader className="sticky top-0 bg-secondary">
              <TableRow>
                <TableHead className="w-[150px]"><User className="inline-block mr-1 h-4 w-4" />Employee</TableHead>
                <TableHead><Calendar className="inline-block mr-1 h-4 w-4" />Date</TableHead>
                <TableHead><Clock className="inline-block mr-1 h-4 w-4" />Time</TableHead>
                <TableHead><MapPin className="inline-block mr-1 h-4 w-4" />Location / Address</TableHead>
                <TableHead className="text-center"><Camera className="inline-block mr-1 h-4 w-4" />Selfie</TableHead>
                <TableHead className="text-center"><Cog className="inline-block mr-1 h-4 w-4" />Method</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {history.map((record) => (
                <TableRow key={record.id}>
                  <TableCell className="font-medium">
                    {record.userName} <br/>
                    <span className="text-xs text-muted-foreground">ID: {getEmployeeOfficialId(record.userId)}</span> <br/>
                    <span className="text-xs text-muted-foreground">Ph: {record.userId}</span>
                  </TableCell>
                  <TableCell>{format(record.timestamp, 'PPP')}</TableCell>
                  <TableCell>{format(record.timestamp, 'p')}</TableCell>
                  <TableCell>
                    <div className="flex flex-col gap-1">
                      <span>
                        Lat: {record.location.latitude.toFixed(4)}, Lon: {record.location.longitude.toFixed(4)}
                      </span>
                      {record.location.address ? (
                        <span className="text-xs text-muted-foreground break-words max-w-[250px]"> {/* Limit width */}
                          {record.location.address}
                        </span>
                      ) : (
                        <span className="text-xs text-muted-foreground italic">Address not recorded</span>
                      )}
                    </div>
                  </TableCell>
                  <TableCell className="text-center">
                    {record.selfie ? (
                       <Popover>
                        <PopoverTrigger asChild>
                           <Image
                            src={record.selfie}
                            alt={`Selfie of ${record.userName || 'user'} on ${format(record.timestamp, 'PPP')}`}
                            width={40}
                            height={40}
                            className="rounded-full object-cover inline-block border cursor-pointer hover:opacity-80 transition-opacity"
                          />
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0">
                           <Image
                              src={record.selfie}
                              alt={`Full selfie of ${record.userName || 'user'}`}
                              width={200} // Adjust size as needed
                              height={200}
                              className="rounded object-cover"
                          />
                        </PopoverContent>
                       </Popover>
                    ) : (
                      'No Selfie'
                    )}
                  </TableCell>
                   <TableCell className="text-center text-xs">{record.attendanceMethod}</TableCell>{/* Display Attendance Method */}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </ScrollArea>
      </CardContent>
      <CardFooter className="flex justify-end">
        <Button onClick={handleDownload} disabled={history.length === 0}>
          <Download className="mr-2 h-4 w-4" />
          Download Processed Report (Excel)
        </Button>
      </CardFooter>
    </Card>
  );
}
